* {
  margin: 0;
  padding: 0;
}
@import url('https://fonts.googleapis.com/css2?family=Open+Sans:ital,wght@0,300..800;1,300..800&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,100..900;1,100..900&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Istok+Web:ital,wght@0,400;0,700;1,400;1,700&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Martel:wght@200;300;400;600;700;800;900&display=swap');


:root {
  --color-primary: #39a4a3;
  --color-secondary: #0d101f;
  --font-primary: "Montserrat", Helvetica, Arial, sans-serif;
}

*::selection {
  background-color: var(--color-primary);
  color: #fff !important;
}


*,
*:before,
*:after {
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}

html {
  font-family: "Montserrat", sans-serif;
  font-size: 62.5% !important; // 1rem = 10px, 10px/16px = 62.5%
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  scroll-behavior: smooth;
  line-height: 1.5;
  color: #545454;
  width: 100%;
  overflow-x: hidden;
}

body {
  font-family: 'Montserrat', sans-serif;
  background-repeat: no-repeat;
  min-height: 100vh;
  max-width: 100%;
  background-color: #fff !important;
  color: #333;
  overflow-x: hidden !important;
  // background-color: #F5F7FA !important;
}

.bg-sec-link{
  background: var(--color-secondary) !important;

  &:hover{
    transition: .2s all ease-in-out;
    background: var(--color-primary) !important;
    color: #fff !important;
    cursor: poIstok Web;
  }
}

.collapse.show{
  z-index: 1000000;
}


.Navbar {
  background: transparent;
  z-index: 10000 !important;
  width: 100%;
  max-width: 100%;
  transition: .5s all ease-in-out;
  padding: 1.5rem 21rem !important;
  background: #fff;
  // position: absolute;
  top: 0;
  left: 0;
  box-shadow: 0 1px 5px 0 rgba(0, 0, 0, .14);

  @media only screen and (max-width: 1600px) {
    padding: 1.5rem 7rem !important;
  }

  @media only screen and (max-width: 1200px) {
    padding: 1.5rem 4rem !important;
  }

  &_main {

  }

  &_image {
    //margin-right: 14rem;

    @media only screen and (max-width: 1400px) {
      display: block;
    }

    @media only screen and (max-width: 990px) {
      display: block;
      margin-right: 0 !important;
    }

    &-img {
      width: 100%;
      max-height: 7.5rem;
      object-fit: contain;

      @media only screen and (max-width: 1400px) {
        width: auto;
      }

      @media only screen and (max-width: 990px) {
        max-height: 6rem !important;
      }

      @media only screen and (max-width: 768px) {
        max-height: 4rem !important;
      }

    }
  }

  &_toggler {
    color: var(--color-primary) !important;

    &-icon {
      font-size: 3rem;

      @media only screen and (max-width: 768px) {
        font-size: 2.4rem;
      }
    }
  }

  &_nav {
    display: flex !important;
    justify-content: flex-end !important;
    width: 100%;
    gap: .5rem;
    transition: all .5s fade-in;

    @media only screen and (max-width: 1300px) {
      gap: .5rem;
    }

    @media only screen and (max-width: 1100px) {
      gap: 1rem;
    }


    @media only screen and (max-width: 990px) {
      background-color: #eee;
      padding: 2rem 3rem !important;
      margin-top: 1rem;
    }

    &-collapse{

      @media only screen and (max-width: 990px) {
        position: absolute !important;
        top: 100% !important;
        left: 0 !important;
        right: 0 !important;
        width: 100% !important;
      }
    }

    &-hr{
      background-color: var(--color-primary);
      color: var(--color-primary);
      height: .2rem;
      margin-bottom: 0 !important;
    }

    &-item {
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      margin: 0 .5rem;

      @media only screen and (max-width: 1600px) {
        margin-right: 2px !important;
        margin-left: 2px;
      }

      @media only screen and (max-width: 990px) {
        justify-content: flex-start !important;
        align-items: flex-start !important;
        text-align: left;
      }
    }

    &-link {
      color: #000;
      font-family: "Montserrat", sans-serif;
      font-size: 1.6rem;
      font-style: normal;
      font-weight: 500;
      line-height: normal;
      text-transform: none;
      white-space: nowrap;

      @media only screen and (max-width: 1200px) {
        font-size: 1.4rem;
      }

      @media only screen and (max-width: 990px) {
        white-space: normal !important;
        font-size: 1.6rem;
        color: #000;
      }  
 

      &:hover {
        width: auto !important;
        color: var(--color-primary) !important;
        cursor: pointer;
      }
    }

    &-link2 {
      color: #000;
      font-family: "Montserrat", sans-serif;
      font-size: 1.6rem;
      font-style: normal;
      font-weight: 500;
      line-height: normal;
      text-transform: none;
      white-space: nowrap;

      @media only screen and (max-width: 1200px) {
        font-size: 1.4rem;
      }

      @media only screen and (max-width: 990px) {
        white-space: normal !important;
        font-size: 1.6rem;
        color: #000;
      }  

      &:hover {
        width: auto !important;
        color: var(--color-primary) !important;
        cursor: pointer;
      }
    }

    &-btn {
      background-color: var(--color-primary);
      font-family: "Montserrat", sans-serif;
      font-size: 1.8rem;
      font-style: normal;
      font-weight: 500;
      line-height: normal;
      border-radius: .5rem;
      padding: 1rem 2rem !important;
      margin-right: 3rem;
      white-space: nowrap;
      color: #fff;
      border: 2px solid var(--color-primary);
      text-transform: none !important;
      transition: all .3s;
      box-shadow: 0 0 2rem .3rem rgba(46, 161, 219, .3);


      @media only screen and (max-width: 1400px) {
        font-size: 1.4rem;
        margin-right: 0rem;
      }

      @media only screen and (max-width: 1200px) {
        margin-right: 0 !important;
        padding: .5rem 1rem !important;
      }

      @media only screen and (max-width: 1100px) {
        font-size: 1rem;
      }

      @media only screen and (max-width: 990px) {
        padding: 1rem !important;
        font-size: 1.4rem;
        color: #000 !important;
        margin-top: 2rem;
      }

      &:hover {
        text-decoration: none;
        color: #fff;
        box-shadow: none;
        transition: all .3s;
        cursor: pointer;
      }
    }

    &-image{
      width: 10rem;
      height: 8.3rem;
      margin-top: 2rem;
      margin-right: 2rem;
      max-width: 10rem;

      &--img{
        object-fit: cover;
        max-width: 10rem;
      }

      &--img2{
        object-fit: cover;
        max-width: 14rem;
        background-color: #fff !important;
      }
    }
  }
}
  
