.header {
  position: relative;
  display: flex;
  height: 100vh; 
  max-height: 100vh; 
  background: #000;
  overflow: hidden;
  z-index: 0;

  @media only screen and (max-width: 1400px) {
    height: 80vh; 
  }

  @media only screen and (max-width: 990px) {
    height: 60vh; 
  }

  @media only screen and (max-width: 768px) {
    height: 40vh; 
  }

  @media only screen and (max-width: 500px) {
    height: 35vh; 
  }

  &::before {
      content: "";
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background: rgba(54, 54, 54, 0.5) !important;
      z-index: -1;
      transition: 0.5s;
  }

  &__background {
      position: absolute;
      top: 50%;
      left: 50%;
      width: 120%;
      height: 0;
      padding-bottom: 66.25%;
      transform: translate(-50%, -50%);
      pointer-events: none;
      z-index: 0;

      &::before {
          content: "";
          position: absolute;
          top: 0;
          left: 0;
          width: 100%;
          height: 100%;
          background: rgba(54, 54, 54, 0.5) !important;
          z-index: -1;
          transition: 0.5s;
      }
  }

  &__video-overlay {
      position: absolute;
      width: 100%;
      height: 100%;
      background-position: center;
      background-size: cover;
      background-repeat: no-repeat;
      transition: opacity 0.5s cubic-bezier(0.250, 0.460, 0.450, 0.940), visibility 0.5s linear;
      z-index: 0;

      &::before {
          content: "";
          position: absolute;
          top: 0;
          left: 0;
          width: 100%;
          height: 100%;
          background: rgba(54, 54, 54, 0.5) !important;
          z-index: -1;
          transition: 0.5s;
      }

      &--fadeOut {
          opacity: 0;
          visibility: hidden;
      }
  }

  iframe {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      pointer-events: none;
      z-index: 0;

      &::before {
          content: "";
          position: absolute;
          top: 0;
          left: 0;
          width: 100%;
          height: 100%;
          background: rgba(54, 54, 54, 0.5) !important;
          z-index: -1;
          transition: 0.5s;
      }
  }

  &__title {
      position: relative;
      flex: 0 0 auto;
      align-self: center;
      width: 100%;
      font-family: 'Permanent Marker', cursive;
      font-size: 50px;
      font-weight: normal;
      text-align: center;
      color: white;
      line-height: 1;
  }
}

