.divider {
  display: block;
  font-size: 0;
  line-height: 0;
  position: relative;
  width: 7.2rem;
  height: .6rem;
  background-color: #3fb9be;
  margin-left: auto;
  margin-right: auto;
  margin-top: 3.1rem;
  margin-bottom: 3.1rem;

  &::before{
    position: absolute;
    left: 100%;
    -webkit-transform: translateY(3px);
    transform: translateY(3px);
    width: 7.2rem;
    height: .1rem;
    background-color: #d5d5d5;
    content: "";
    display: inline-block;
    margin-top: 0;
    margin-bottom: 0;
  }

  &::after{
    position: absolute;
    right: 100%;
    -webkit-transform: translateY(3px);
    transform: translateY(3px);
    width: 7.2rem;
    height: .1rem;
    background-color: #d5d5d5;
    content: "";
    display: inline-block;
    margin-top: 0;
    margin-bottom: 0;
  }
}


